export const STORY_ONE = "In the heart of a bustling city, where the rhythms of life blend into a symphony of sounds, there exists a hidden garden. Its pathways, lined with vibrant wildflowers, wind through delicate arches and beneath ancient trees. Sunlight dances upon the surface of a tranquil pond, where koi fish glide like living strokes of paint. The scent of blooming jasmine hangs in the air, mingling with the laughter of children chasing butterflies. As day turns into night, the garden undergoes a transformation. Lanterns suspended from branches cast a warm and inviting glow, guiding visitors along new, enchanting paths. Fireflies join in the dance, their tiny lights creating a magical ambiance. Underneath a celestial canopy of stars, people gather to share stories and dreams, finding solace in the serenity this oasis offers amidst the urban chaos. Each visit to this secret garden reveals new wonders, a testament to the beauty that can flourish even in the most unexpected places."

